.container {
  padding-left: 20px;
  padding-right: 20px;
  @media screen and (min-width: 320px) {
    width: 320px;
    margin: 0 auto;
  }
  @media screen and (min-width: 480px) {
    width: 480px;
    padding-right: 40px;
    padding-left: 40px;
  }
  @media screen and (min-width: 768px) {
    width: 768px;
  }
  @media screen and (min-width: 1200px) {
    width: 1200px;
  }
}
